@import 'components/variables';

.section-welcome {
  background-image: url(../images/bg-welcome.png);
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 100px;
  padding-bottom: 6rem;
  display: flex;
  align-items: center;
  @media (min-width: 992px) {
    padding-top: 150px;
  }
  .title {
    text-align: center;
    @media (min-width: 768px) {
      text-align: start;
    }
  }
}
.section-combo {
  background-color: #F5F8FD;
  .container {
    background: linear-gradient(to bottom, rgba(150,221,241,1) 0%, rgba(255,255,255,1) 8%, rgba(255,255,255,1) 12%, rgba(255,255,255,1) 100%);
    border-radius: 15px;
    transform: translateY(-4rem);
    .content-menu {
      padding: 4rem 1rem;
      @media (min-width: 425px) {
        padding: 4rem 3rem;
      }
      .col-combos {
        border-bottom: 1px dotted $text-gray;
        text-align: center;
        padding-top: 4rem;
        padding-bottom: 3rem;
        .img-container {
          transform: rotate(45deg);
          -ms-transform: rotate(45deg);
          -webkit-transform: rotate(45deg);
          -moz-transform: rotateX(45deg);
          -o-transform: rotateX(45deg);
          position: relative;
          height: 100px;
          width: 100px;
          margin: 0 auto;
          padding: 0;
          cursor: pointer;
          @media (min-width: 768px) {
            height: 150px;
            width: 150px;
          }
          .img-rhombus {
            overflow: hidden;
            height: 100%;
            width: 100%;
            display: inline-block;
            margin: 2%;
            padding: 0;
            border-radius: 10px;
            .inner-wrapper {
              position: relative;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              -ms-transform: translate(-50%, -50%);
              -webkit-transform: translate(-50%, -50%);
              -moz-transform: translate(-50%, -50%);
              -o-transform: translate(-50%, -50%);
              width: 160%;
              height: 160%;
              position: relative;
              transition: all 0.5s linear;
              img {
                transform: rotate(-45deg);
                -ms-transform: rotate(-45deg);
                -webkit-transform: rotate(-45deg);
                -moz-transform: rotateX(-45deg);
                -o-transform: rotateX(-45deg);
                width: 100%;
                height: 100%;
                margin: 0;
                padding: 0;
              }
            }
          }
        }
      }
    }
  }
}
.placa-menu {
  content: "";
  background: rgb(255,255,255);
  background: linear-gradient(90deg, rgba(250, 214, 9, 0.7) 0%, rgba(255,255,255,0) 150%);
  transform: rotate(-45deg);
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 99999;
  opacity: 0;
}
#menu-combos {
  .modal-content {
    .img-menu-modal {
      object-fit: cover;
      width: 100%;
      height: 40vh;
      @media (min-width: 992px) {
        width: 100%;
        height: 100%;
      }
    }
    .title-modal {
      font-size: 1.5rem;
      color: $yellow;
      font-weight: bold;
    }
    ol {
      padding-left: 1rem;
      li::marker {
        color: $blue;
        font-weight: 600;
      }
      li {
        color: $text-gray;
      }
    }
  }
}